import React, { Component } from 'react';

import Link from 'next/link';
import HeaderComponent from "../client/layout/header/header.js";
import FooterComponent from '../client/layout/footer/footer.js';
import TwitterNoteComponent from '../client/components/twitterNote/twitterNote.js';

import { Http } from '../client/utils.js';

export default class HomePage extends Component {
    static async getInitialProps() {
        let res = await Http.get("/notes/notesList");
        return {
            notesList: res['twitterNotes']
        };
    }

    render() {
        return <div className="">
            <HeaderComponent />
            <div className="container markdown-body">
                {
                    this.props.notesList.map((note) => {
                        return <div className="note-container" key={note.key}>
                            <TwitterNoteComponent note={note}/>
                          </div>;
                    })
                }
            </div>
            <FooterComponent />
        </div>;
    }
} 
